/**
 * @component CityPicker
 * @description
 *
 * ##  / CityPicker
 *
 * ### 
 *
 * /, fetchData, .
 *
 * ### fetchData
 *
 * fetchDatacodepicker, , , . , :
 *
 *  / Name |  / Type |  / Description
 * ----------|-----------|------------------
 * text      | string    | 
 * value     | *         | 
 * disabled  | boolean   | 
 *
 * ### 
 * ```
 * import { CityPicker } from 'vimo/components/city-picker'
 * ```
 *
 * @props {Array} selectedCity - , , : ['',''] ['','',''], 
 * @props {String/Number} [startCode='1'] - code,   1
 * @props {Function} [onCancel] - 
 * @props {Function} [onSelect] - 
 * @props {Function} fetchData - , funtioncodepromise, 
 *
 * @demo https://dtfe.github.io/vimo-demo/#/picker
 * @usage
 * function openCityPicker () {
 *    CityPicker.present({
 *      onSelect (data) {
 *        console.log('onSelect')
 *        console.log(data)
 *      },
 *      onCancel () {
 *        console.log('onCancel')
 *      },
 *      startCode: '1',
 *      selectedCity: ['', ''],
 *      fetchData (code) {
 *        return new Promise((resolve, reject) => {
 *          if (code) {
 *            axios(`static/address-data/${code}.json`)
 *            .then((response) => {
 *              response.data.forEach((item) => {
 *                item.text = item.divisionName
 *                item.value = item.divisionCode
 *                item.disabled = false
 *              })
 *              resolve(response.data)
 *            })
 *            .catch(() => {
 *              resolve([])
 *              console.error('')
 *            })
 *          } else {
 *            resolve([])
 *            console.error('code')
 *          }
 *        })
 *      }
 *    })
 *  }
 *
 */
import { isArray } from '../../util/util'
import { Picker } from '../picker'
const CityPicker = {
  present (options) {
    let startCode = options.startCode || '1'              //   1
    let selectedCity = options.selectedCity
    // null number/string/any [] [''] ['',''] ['1','2','3','4']
    if (!selectedCity || !isArray(selectedCity) || isArray(selectedCity) && (selectedCity.length === 0 || selectedCity.length > 3)) {
      // ,   
      selectedCity = ['110000', '110100']
    }

    if (isArray(selectedCity)) {
      if (selectedCity.length === 2 && !selectedCity[0]) {
        selectedCity = ['110000', '110100']
      }
      if (selectedCity.length === 3 && (!selectedCity[0] || !selectedCity[1])) {
        selectedCity = ['110000', '110100', '110101']
      }
    }

    let provinceCode = selectedCity[0]  //  110000, 
    let cityCode = selectedCity[1]      //  110100 
    let districtCode = selectedCity[2]
    let defaultFetchData = function () { return new Promise(resolve => resolve([])) }
    let fetchData = options.fetchData || defaultFetchData
    let columns = []
    fetchData(startCode).then((data) => {
      if (data.length > 0) {
        //  province 
        columns = [
          {
            name: 'province',
            selectedIndex: getCodeIndex(provinceCode, data),
            align: 'right',
            options: data
          }
        ]
        console.assert(provinceCode, 'provinceCode, !')
        fetchData(provinceCode).then((data) => {
          //  city 
          columns.push({
            name: 'city',
            selectedIndex: getCodeIndex(cityCode, data),
            align: 'left',
            options: data
          })

          if (districtCode) {
            fetchData(cityCode).then((data) => {
              //  district 
              columns.push({
                name: 'district',
                selectedIndex: getCodeIndex(districtCode, data),
                align: 'left',
                options: data
              })

              initPicker(columns)
            })
          } else {
            initPicker(columns)
          }
        })
      } else {
        console.error('CityPicker, ')
      }
    })

    /**
     * codedata
     * @private
     * */
    function getCodeIndex (code = '', data) {
      code = code.toString()
      if (code) {
        for (let i = 0, len = data.length; len > i; i++) {
          let item = data[i]
          if (item.value && item.value.toString() === code) {
            return i
          }
        }
      }
      return 0
    }

    /**
     * Picker
     * @private
     * */
    function initPicker (columns) {
      if (columns.length > 2) {
        columns.forEach((column) => {
          column.optionsWidth = '80px'
        })
      }

      let data = {
        cssClass: 'small-text',
        buttons: [
          {
            role: 'cancel',
            text: '',
            handler () {
              'use strict'
              options.onCancel && options.onCancel()
            }
          },
          {
            text: '',
            handler: (data) => {
              options.onSelect && options.onSelect(data)
            }
          }
        ],
        columns,
        onSelect: onSelectHandler
      }

      Picker.present(data)
    }

    /**
     * Picker
     * @private
     * */
    function onSelectHandler (data) {
      if (data.columnIndex === 0) {
        fetchData(data.value).then((data) => {
          columns[1].options = data
          Picker.resetColumn(1)
          if (columns[1].options[0] && selectedCity.length > 2) {
            fetchData(columns[1].options[0].value).then((data) => {
              columns[2].options = data
              Picker.resetColumn(2)
            })
          }
        })
      }
      if (data.columnIndex === 1 && selectedCity.length > 2) {
        fetchData(data.value).then((data) => {
          columns[2].options = data
          Picker.resetColumn(2)
        })
      }
    }
  }
}
export { CityPicker }


