<template>
    <Page>
        <Header>
            <Navbar>
                <Title></Title>
            </Navbar>
        </Header>
        <Content padding>

            <h5></h5>
            <p>
                , , Picker, better-picker.
            </p>

            <p>, : Picker, cityCode; better-pickercityCode, .</p>

            <h5>Picker</h5>
            <p><strong>: </strong>{{selectedCity}}</p>
            <p><strong></strong>: {{province}}-{{city}}-{{district}}</p>
            <Button block @click="openCityPicker"></Button>

            <h5>Better-Picker</h5>
            <p><strong>: </strong>{{bSelectedCity}}</p>
            <p><strong></strong>: {{bProvince}}-{{bCity}}-{{bDistrict}}</p>
            <Button block @click="openCityBetterPicker"></Button>
        </Content>
    </Page>
</template>
<style scoped lang="scss"></style>
<script type="text/javascript">
  import { RegionPicker } from 'vimo/components/region-picker'
  import { Picker } from 'vimo/components/picker'
  import { CityPicker } from 'vimo/components/city-picker'
  import axios from 'axios'
  export default{
    name: 'name',
    data () {
      return {
        selectedCity: ['140000', '140100', '140106'],
        province: '',
        city: '',
        district: '',

        // -----
        bSelectedCity: ['220000', '220200', '220283'],
        bProvince: '',
        bCity: '',
        bDistrict: ''
      }
    },
    props: {},
    watch: {},
    computed: {},
    methods: {
      /**
       * Picker
       * */
      openCityPicker () {
        const _this = this
        CityPicker.present({
          onSelect (data) {
            let tmp = []
            data.province && (_this.province = data.province.text, tmp.push(data.province.value.toString()))
            data.city && (_this.city = data.city.text, tmp.push(data.city.value.toString()))
            data.district && (_this.district = data.district.text, tmp.push(data.district.value.toString()))
            _this.selectedCity = tmp
          },
          onCancel () {
            console.log('onCancel')
          },
          startCode: '1',
          selectedCity: this.selectedCity,
          fetchData (code) {
            return new Promise((resolve) => {
              if (code) {
                axios(`static/address-data/${code}.json`)
                .then((response) => {
                  response.data.forEach((item) => {
                    item.text = item.divisionName
                    item.value = item.divisionCode
                    item.disabled = false
                  })
                  resolve(response.data)
                })
                .catch(() => {
                  resolve([])
                  console.error('')
                })
              } else {
                resolve([])
                console.error('code')
              }
            })
          }
        })
      },

      // ----- better-picker -----
      openCityBetterPicker () {
        const _this = this
        RegionPicker.present({
          selectedCity: _this.bSelectedCity,
          title: '',
          onSelect (data) {
            console.log(data)
            _this.bProvince = `${data[0].name}`
            _this.bCity = `${data[1].name}`
            _this.bDistrict = `${data[2].name}`
            _this.bSelectedCity = [data[0].value, data[1].value, data[2].value]
          },
          onCancel (data) {
            console.log('onCancel:' + JSON.stringify(data))
          }
        })
      }
    },
    created () {

    },
    mounted () {

    },
    activated () {},
    components: {RegionPicker, Picker}
  }


</script>



